.app-notes {
  width: 80%;
  margin: 2rem auto;
}
.app-notes-main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.app-notes-main .item {
  position: relative;
  flex: 1 1;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  max-width: 350px;
}
.app-notes-main .item-bg {
  padding: 0.2rem;
  border: 1px solid rgba(4, 8, 9, 0.5);
  position: absolute;
  left: 50%;
  margin-left: -1.2rem;
  z-index: -1;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.app-notes-main .item-in {
  width: 2rem;
  height: 2rem;
  border: 1px solid rgba(4, 8, 9, 0.5);
}
.app-notes-main .item-content {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  font-size: 0.3rem;
}
